.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.nav-item {

}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.nav {
      
}
.nav-item{
    font-size: 1.4rem;
}
.profilePicture {
    max-width: 20%;
    margin-bottom: 40px;
}
.aboutHeader{
    text-align: start;
}
h2 {
    padding: 20px;
}
.info {
    text-align: left;
    font-size: 1.5rem;
    line-height: 2rem;
    min-width: 100px;
}
.about {
    padding: 10px;
    display: flex;
    margin: 0, 20px;
}
.aboutPicture {
    max-width: 20%;
}

.skills {
  margin: 50px;
  padding: 50px;
}

.resumePicture {
    max-width: 20%;
}
.contact {
    margin:2%
}
.project-cards {
    display: flex;
    flex-wrap: wrap;
    margin: 20px;
}
.project {
    padding: 50px 50px;
}

.project-link {
    padding: 10px;
    color: var(--light);
    font-size: x-large;
}

.project-img {
    min-width: 200px;
    max-width: 500px;
}

@media screen and (min-width: 992px) {
    .project-img {
        min-width: 200px;
        max-width: 400px;
    }
}

@media screen and (min-width: 768px) {
    .project-img {
        min-width: 100%;
    }
}
.header-h1 {
    position: relative;
    z-index: 2;  
}
.cover-img {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    height:200px;
    width: 100%;
    -webkit-filter: sepia(1) opacity(60%);
            filter: sepia(1) opacity(60%);
    opacity: 0.5;
}

.footer {
    height: 75px;
}
.footer-link{
    margin: 10%;
}
:root {
  --primary: #ff54d1;
  --secondary: #3c313d;
  --light: #fafafa;
  --dark: #ab5693;
  --darker: #4f192f;
  --font-family: 'Bellota', cursive;
  /* --font-family: 'Mochi Pop One', cursive; */
} 

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  background: #4f192f;
  background: var(--darker);
  color: #fafafa;
  color: var(--light);
  font-family: 'Bellota', cursive;
  font-family: var(--font-family);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Bellota', cursive;
  font-family: var(--font-family);
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 5rem;
  margin: 50px 20px;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.2rem;
  margin: 20px;
  line-height: 2rem;
}

